.header {
  background-color: #570f32;
  color: white;
  padding: 30px 20px;
  text-align: center;

  h1 {
    font-size: 2.5rem;
    margin: 0;
    color: #f9dcf8e9;
  }

  .typed-text {
    font-size: 1.2rem;
    color: #7fabdb;
    margin-top: 10px;
  }

  img {
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.4);
    }

  p {
    font-size: 1.2rem;
    color: #ccc;
  }

  nav {
    margin: 20px 0;
    a {
      color: #f9dcf8e9;
      margin: 0 15px;
      font-weight: bold;
      text-decoration: none;
      transition: color 0.3s;
    }
    a:hover {
      color: #7fabdb;
    }
  }

  .social-icons a {
    color: #ccc;
    font-size: 1.5rem;
    margin: 0 10px;
    transition: color 0.3s;
  }

  .social-icons a:hover {
    color: #7fabdb;
  }
}
